<template>
  <div>
    <core-toolbarinstitution />
    <v-container
      fill-height
      fluid
      grid-list-xl
    >
      <v-layout
        justify-center
        wrap
      >
        <v-flex>
          <material-card class="v-card-profile">
            <v-avatar
              slot="offset"
              class="profile"
              color="grey"
              size="164"
              tile
            >
              <img
                :src="imageSrc.url"
                height="200"
                name="image"
              >
            </v-avatar>
            <v-card-text class="text-xs-center">
              <h6 class="category text-gray font-weight-thin mb-3">
                Program Name
              </h6>
              <h4 class="card-title font-weight-light">
                {{ name }}
              </h4>
              <ul
                id="infoList"
                style="list-style-type:none;"
              >
                <li>
                  PROGRAM ID: {{ id }}
                </li>
                <li>
                  TYPE: {{ password }}
                </li>
                <li>
                  FEES: ${{ location.city }}
                </li>
                <li>
                  METHOD OF DELIVERY: {{ location.country }}
                </li>
                <li>
                  INSTITUTION ID: {{ email }}
                </li>
              </ul>
            </v-card-text>
          </material-card>

          <v-card>
            <v-card-title>
              <v-text-field
                v-model="search"
                label="Search"
                single-line
                hide-details
              />
            </v-card-title>
            <v-data-table
              dense="true"
              :headers="headers"
              :items="items"
              :search="search"
            />
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>


<script>

export default {
  name: 'InstitutionLoggedIn',
  data () {
    return {
    imageSrc:{
    url : "require(@/assets/defaultProfile.png)"
    },
      id: '',
      name: '',
      phoneNumber: '',
      email: '',
      password: '',
      location : {
        city: '',
        country: '',
        address: ''
      },
      search: '',
      headers: [
        {
          text: 'Course Code',
          align: 'start',
          filterable: false,
          value: 'name',
        },
        { text: 'Course ID', value: 'calories' },
          { text: 'Highschool', value: 'iron' },
          { text: 'End Date', value: 'carbs' },
          { text: 'Start Date', value: 'protein' },
      ],
      items: [
        {
        }
      ],
    }
  },
async mounted(){
if(this.$store.state.redirected == true){
console.log("hey that worked");
var  carriedObj = this.$store.state.carriedObject;

this.name = carriedObj.name;
   this.id = carriedObj.calories;
  this.email = carriedObj.fat;
  this.password = carriedObj.carbs;
  this.location.city = carriedObj.protein;
  this.location.country = carriedObj.iron;
  this.$store.dispatch('wasCarried');
};
this.$store.commit('wasCarried');

console.log("IMPORTANT program id: " + this.id);
let axios = require('axios');
let axiosConfig = {headers:{'Authorization': 'Bearer ' + this.$store.state.token}};
await axios.get('http://localhost:8080/institutions/allCoursesOfProgram/' + this.id, axiosConfig).then(resp => {var display = this.items; function displayCourses(item){/* display[index].name = item.name; display[index].calories = item.id; display[index].fat = item.institutionId; display[index].carbs = item.startDate; display[index].protein = item.endDate; */ var entry = {name : item.name, calories: item.id, fat : item.courseType, carbs : item.startDate, protein : item.endDate, iron:  item.highSchool}; display.push(entry); }
console.log("DATA: " + resp.data[0].endDate);
resp.data.forEach(displayCourses);


}).catch(error => {
   console.log(error);
})

  axios.get('http://localhost:8080/institutions/loadimage/111', axiosConfig).then(resp => {this.imageSrc.url = "data:image/png;base64," + resp.data;});
}
}
</script>
